import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    playing: null,
    artist: null
  },
  getters: {
    playingSong: state => (state.playing ? state.playing : ''),
    playingArtist: state => (state.artist ? state.artist : '')
  },
  mutations: {
    SWITCH_SONG (state, payload) {
      state.playing = payload.songName
      state.artist = payload.songArtist
    }
  }
})
